body {
  margin: 0;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.h2 {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 25px;
  line-height: 30px;
  font-weight: bold;
  color: black;
  margin: 0;
}
@media (min-width: 768px) {
  .h2 {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
