}
.page-header__container {
  width: 100%;
  min-width: 320px;
  max-width: 550px;
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  color: #000000;
}
.page-header__logo-wrapper {
  width: 100%;
  padding: 21px 0 22px;
  display: flex;
  justify-content: center;
  border-top: 1px solid #ececec;
  border-bottom: 1px solid #ececec;
}
.page-header__logo-image {
  display: block;
}
.page-header__logo--link:hover {
  opacity: 0.6;
}
.page-header__logo--link:active {
  opacity: 0.3;
}
.page-header__btn-menu {
  display: none;
  position: absolute;
  width: 20px;
  height: 14px;
  background-color: transparent;
  border: none;
  border-top: 2px solid #221f1f;
  border-bottom: 2px solid #221f1f;
  top: 32px;
  right: 10%;
  overflow: hidden;
  outline: none;
}
.page-header__btn-menu::after {
  content: "";
  display: block;
  position: relative;
  width: 27px;
  height: 2px;
  background-color: #221f1f;
  top: 0;
  left: -8px;
}
.page-header__btn-menu--close {
  border: none;
  height: 23px;
  width: 23px;
  top: 27px;
  right: 9%;
}
.page-header__btn-menu--close::after {
  left: -9px;
  top: -1px;
  transform: rotate(45deg);
}
.page-header__btn-menu--close::before {
  content: "";
  display: block;
  position: relative;
  width: 27px;
  height: 2px;
  background-color: #221f1f;
  top: 1px;
  left: -9px;
  transform: rotate(-45deg);
}
.page-header__btn-menu:hover {
  opacity: 0.6;
}
.page-header__btn-menu:active {
  opacity: 0.3;
}
.page-header__search {
  position: relative;
}
.page-header__cart {
  position: relative;
}
.page-header__nav--mobile {
  position: absolute;
  left: 0;
  bottom: -158px;
  background-color: white;
  z-index: 2;
}
.page-header__search--mobile {
  position: absolute;
  left: 0;
  bottom: -237px;
  z-index: 2;
}
.page-header__cart--mobile {
  position: absolute;
  left: 0;
  bottom: -316px;
  z-index: 2;
}
.page-header__bottom {
  display: none;
}
.page-header__profit {
  display: none;
}
@media (min-width: 768px) {
  .page-header__container {
    max-width: 950px;
    padding: 0;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
  }
  .page-header__nav {
    order: 1;
  }
  .page-header__logo-wrapper {
    width: auto;
    border-top: none;
    padding: 22px 0 20px 6.51%;
    justify-content: flex-start;
    flex-grow: 1;
    background-color: white;
  }
  .page-header__logo-image {
    width: 112px;
    height: auto;
  }
  .page-header__btn-menu {
    display: none;
  }
  .page-header__bottom {
    order: 2;
    display: flex;
  }
  .page-header__delivery {
    display: block;
    order: 2;
    font-weight: normal;
    font-size: 15px;
    color: #444444;
    padding: 17px 50px 0 11px;
    margin-left: auto;
  }
  .page-header__delivery:hover {
    opacity: 0.6;
  }
  .page-header__delivery:active {
    opacity: 0.3;
  }
}
@media (min-width: 768px) and (max-width: 1149px) {
  .page-header {
    background-image: linear-gradient(to bottom, white 88px, #f9f9f9 88px);
  }
  .page-header--pages {
    background-image: linear-gradient(to bottom, white 88px, #f1f1f1 88px);
  }
}
@media (min-width: 1150px) {
  .page-header__container {
    max-width: 1450px;
  }
  .page-header__logo-wrapper {
    padding: 0;
    align-items: center;
    justify-content: center;
  }
  .page-header__logo {
    margin-right: 95px;
  }
  .page-header__logo-image {
    width: 148px;
  }
  .page-header__bottom {
    width: 100%;
    background-color: #f9f9f9;
    position: relative;
  }
  .page-header__bottom::before {
    content: "";
    display: block;
    position: absolute;
    height: 100%;
    width: calc(50vw - 50%);
    left: calc(-1 * (50vw - 50%));
    top: 0;
    background-color: #f9f9f9;
  }
  .page-header__bottom::after {
    content: "";
    display: block;
    position: absolute;
    height: 100%;
    width: calc(50vw - 50% - 9px);
    right: calc(-1 * (50vw - 50% - 9px));
    top: 0;
    background-color: #f9f9f9;
  }
  .page-header__bottom--pages {
    background-color: #f1f1f1;
  }
  .page-header__bottom--pages::before,
  .page-header__bottom--pages::after {
    background-color: #f1f1f1;
  }
  .page-header__profit {
    max-width: 74%;
    display: flex;
    flex-wrap: wrap;
    font-size: 15px;
    list-style: none;
    margin: 0;
    padding: 0;
    padding-left: 50px;
    padding-top: 17px;
  }
  .page-header__link {
    color: black;
    padding-right: 60px;
  }
  .page-header__link:hover {
    opacity: 0.6;
  }
  .page-header__link:active {
    opacity: 0.3;
  }
}
