.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 9;
  animation-name: modal-show;
  animation-duration: 0.3s;
}
.modal__wrapper {
  position: fixed;
  width: 320px;
  background-color: white;
  box-shadow: 0 4px 8px 0 lightgray;
  z-index: 10;
  left: 50%;
  top: 40%;
  margin-left: -160px;
  margin-top: -166px;
}
.modal--show {
  display: block;
}
.modal__close {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 15px;
  right: 15px;
  border: none;
  background-color: white;
  cursor: pointer;
  outline: none;
}
.modal__close::after,
.modal__close::before {
  content: "";
  display: block;
  position: relative;
  width: 27px;
  height: 2px;
  background-color: #63d1bb;
  left: -9px;
  top: -1px;
  transform: rotate(45deg);
}
.modal__close::before {
  top: 1px;
  left: -9px;
  transform: rotate(-45deg);
}
.modal__close:hover::before,
.modal__close:focus::before,
.modal__close:hover::after,
.modal__close:focus::after {
  background-color: orange;
}
.modal__close:active::before,
.modal__close:active::after {
  opacity: 0.7;
}
