.main-nav {
  width: 100%;
}

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.main-nav__item {
  border-bottom: 1px solid #ececec;
}

.main-nav__link {
  display: block;
  padding: 26px 0 28px 23.5%;
  color: #000000;
}

.hao(~".main-nav__link");

@media (min-width: 768px) {
  .main-nav {
    width: auto;
    max-width: 62%;
  }

  .main-nav__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .main-nav__item {
    padding: 0;
    border: none;
  }

  .main-nav__link {
    padding: 0;
    padding-right: 35px;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .main-nav {
    margin-right: auto;
    min-width: 56%;
    padding-left: 6.51%;
  }

  .main-nav__list {
    font-size: 15px;
    padding: 17px 0;
  }
}

@media (min-width: 1150px) {
  .main-nav {
    order: -1;
    padding-left: 50px;
    border-bottom: 1px solid #ececec;
    background-color: white;
    max-width: 50%;
  }

  .main-nav__list {
    font-size: 17px;
    padding: 32px 0;
  }

  .main-nav__link {
    padding-right: 70px;
  }
}
